@import "./components/header";
@import "zeroing";
@import "important";
@import "font-face";
@import "mixins";

.container {
    width: 1170px;
    margin: 0 auto;
}
.header .burger__menu._active .burger__container .burger__navigation .link__four {
    color: #FEAB47;
}
.header .header__section .header__navigation .link__four {
    color: #FEAB47;
}
.section {
    margin: 60px 0px 120px 0px;
    h1 {
        text-align: center;
    }
}
.container__news {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 40px;
    span {
        font-family: 'Nunito';
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 19px;
        color: #000000;
        padding-bottom: 25px;
    }
    .news__company {
        display: grid;
        grid-template-columns: 1fr 2fr 1fr;
        .card__new {
          position: relative;
          width: 270px;
          height: 350px;
          background: #ffffff;
          box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.25);
          border-radius: 10px 0px 0px 10px;
          img {
            position: absolute;
            left: 20px;
            top: 20px;
            width: 85%;
          }
          p {
            position: absolute;
            width: 85%;
            height: 80px;
            left: 20px;
            top: 195px;
            font-family: "Nunito";
            font-style: normal;
            font-weight: 600;
            font-size: 15px;
            line-height: 20px;
            color: #000000;
          }
          .line {
            position: absolute;
            left: 175px;
            top: 330px;
            width: 74.5px;
            border: 2px solid #FEAB47;
            border-radius: 5px;
          }
        }
        .card__new:hover {
          cursor: pointer;
          box-shadow: -1px 0px 5px 1px rgba(0, 0, 0, 0.25);
          transition: all 600ms;
        }
        .card__news-two {
          display: grid;
          grid-template-rows: repeat(2, 1fr);
          gap: 30px;
          padding: 0px 30px;
          .card__new {
            position: relative;
            width: 370px;
            height: 160px;
            background: #ffffff;
            box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.25);
            border-radius: 2px;
            p {
              position: absolute;
              width: 330px;
              height: 80px;
              left: 20px;
              top: 20px;
              font-family: "Nunito";
              font-style: normal;
              font-weight: 400;
              font-size: 15px;
              line-height: 20px;
              color: #000000;
            }
            .line {
                position: absolute;
                left: 270px;
                top: 140px;
                width: 74.5px;
                border: 2px solid #FEAB47;
                border-radius: 5px;
              }
          }
          .card__new:hover {
            cursor: pointer;
            box-shadow: -1px 0px 5px 1px rgba(0, 0, 0, 0.25);
            transition: all 600ms;
          }
        }
        .card__new-big {
          position: relative;
          width: 470px;
          height: 350px;
          background: #ffffff;
          box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.25);
          border-radius: 0px 10px 10px 0px;
          img {
            position: absolute;
            left: 20px;
            top: 20px;
            width: 90%;
          }
          p {
            position: absolute;
            width: 429px;
            height: 80px;
            left: 20px;
            top: 210px;
            font-family: "Nunito";
            font-style: normal;
            font-weight: 400;
            font-size: 15px;
            line-height: 20px;
            color: #000000;
          }
          .line {
            position: absolute;
            left: 380px;
            top: 330px;
            width: 74.5px;
            border: 2px solid #FEAB47;
            border-radius: 5px;
          }
        }
        .card__new-big:hover {
          cursor: pointer;
          box-shadow: -1px 0px 5px 1px rgba(0, 0, 0, 0.25);
          transition: all 600ms;
        }
    }
    
}
.container__news-two {
    .news__company {
        .card__new {
            border-radius: 0px 10px 10px 0px;
        }
        .card__news-two {
            display: grid;
            grid-template-rows: repeat(2, 1fr);
            gap: 30px;
            padding: 0px 30px;
            .card__new {
              position: relative;
              width: 370px;
              height: 160px;
              background: #ffffff;
              box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.25);
              border-radius: 2px;
              p {
                position: absolute;
                width: 330px;
                height: 80px;
                left: 20px;
                top: 20px;
                font-family: "Nunito";
                font-style: normal;
                font-weight: 400;
                font-size: 15px;
                line-height: 20px;
                color: #000000;
              }
              .line {
                  position: absolute;
                  left: 270px;
                  top: 140px;
                  width: 74.5px;
                  border: 2px solid #FEAB47;
                  border-radius: 5px;
                }
            }
            .card__new:hover {
              cursor: pointer;
              box-shadow: -1px 0px 5px 1px rgba(0, 0, 0, 0.25);
              transition: all 600ms;
            }
          }
        .card__new-big {
            border-radius: 10px 0px 0px 10px;
        }
    }
}
.container__news-three {
    .news__company {
        .card__news-two {
            padding: 0px 0px;
        }
        .card__new-big {
            border-radius: 0px 0px 0px 0px;
            margin: 0px 30px;
        }
        .card__new {
            border-radius: 0px;
            p {
                top: 25px;
                width: 85%;
            }
        }
    }
}
.pages {
    padding-top: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    img {
        cursor: pointer;
        padding: 0px 25px;
    }
    .container__number {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 190px;
        a {
            cursor: pointer;
            font-family: 'Nunito';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            color: #FFFFFF;
            line-height: 22px;
            padding-top: 2px;
        }
        .number {
            width: 26px;
            height: 26px;
            background: #E27E36;
            border-radius: 5px;
        }
        .number.active{
            width: 26px;
            height: 26px;
            background: #383838;
            border-radius: 19px;
        }    
    }
}

.container__new {
    display: none;
}
.container__new.active {
    display: block;
}


@import "./components/footer";
@import "./breakpoints/breakpoint-news";
.footer .footer__container .footer__section .footer__navigation .link__four {
    color: #FEAB47;
}
.footer .footer__container-phone .footer__section .footer__section-left .footer__navigation .link__four {
    color: #FEAB47;
}